.Home {
    display: flex;
    justify-content: center;
}

.home-box {
    width: fit-content;
    margin-top: 1.5cm;
}

.name {
    font-weight: 1000;
    font-size: 100px;
    font-family: changa;
    background: hsla(291, 79%, 51%, 1);
    background: linear-gradient(90deg, rgb(175, 7, 205) 0%, rgb(78, 78, 218) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    /* For better browser support */
    color: transparent;
    /* Hide the actual text */
}